/**
 * @file main.js
 * @author djmughal
 * @description notify init instance
 */
import Vue from 'vue';
import Component from './mixin';

// new Vue
const NotifyConstructor = Vue.extend(Component);
let seed = 1;
const instances = [];

export default options => {
    // dom
    if (Vue.prototype.$isServer) {
        return;
    }

    // 
    const {
        autoClose,
        ...rest
    } = options;

    // instance Vue
    // jsObject.assgin()
    // instance this
    const instance = new NotifyConstructor({
        propsData: {
            ...rest
        },
        data: {
            autoClose: autoClose === undefined ? 3000 : autoClose
        }
    });

    //  instance
    const removeInstance = instance => {
        if (!instance) {
            return;
        }
        const len = instances.length;
        const index = instances.findIndex(item => item.id === instance.id);
        instances.splice(index, 1);

        if (len <= 1) {
            return;
        }

        const removeHeight = instance.vm.height;
        for (let i = index; i < len - 1; i++) {
            instances[i].verticalOffset
                = parseInt(instances[i].verticalOffset, 10) - removeHeight - 16;
        }
    };

    // -------------------------------------
    // id 
    const id = `NotifyConstructor_${seed++}`;
    instance.id = id;
    instance.vm = instance.$mount(); // html
    document.body.appendChild(instance.vm.$el);

    // dataprops
    let verticalOffset = 0;
    instances.forEach(item => {
        verticalOffset += item.$el.offsetHeight + 10;
    });
    verticalOffset += 10;
    instance.verticalOffset = verticalOffset;
    instances.push(instance);

    // $emit
    instance.vm.$on('closeNotify', () => {
        removeInstance(instance); // instance
        document.body.removeChild(instance.vm.$el); // dom
        instance.vm.$destroy(); // 
    });

    // $emit
    instance.vm.$on('leave', () => {
        removeInstance(instance); // instance
        document.body.removeChild(instance.vm.$el); // dom
        instance.vm.$destroy(); // 
    });
    return instance.vm;
};
